  }
}

@media screen and (max-width: 550px) {
  .cards__item {
    margin-left: -2rem;
    margin-right: -2rem;
  }

  .cards__item__info {
    padding: 10px 20px 20px;
  }

  .cards__item__text {
    color: #e8e8e8;
    font-size: 15px;
    line-height: 20px;
  }
}

